import React from 'react';

const KnowledgeItem = ({title, imageUrl, percentage}) => {
    return (
        <article className="knowledge__list__item" id={title}>
            <figure className="knowledge__list__item__picture">
                        <img src={imageUrl} alt={title}/>
                        <figcaption>{`${percentage}%`}</figcaption>
            </figure>
            <h3 className="knowledge__list__item__title">{title}</h3>
        </article>
    );
};

export default KnowledgeItem;